// ** React Imports
import { useEffect, useState, Fragment } from "react"
import { useParams, Link } from "react-router-dom"
import UILoader from "@components/ui-loader"
import Spinner from "@components/spinner/Loading-spinner"
// ** Store & Actions
import { getUser } from "../store"
import { useSelector, useDispatch } from "react-redux"

// ** Reactstrap Imports
import { Row, Col, Alert, CardText } from "reactstrap"

// ** User View Components
import UserTabs from "./Tabs"
// import PlanCard from "./PlanCard"
import UserInfoCard from "./UserInfoCard"

// ** Styles
import "@styles/react/apps/app-users.scss"

import { useTranslation } from "react-i18next"

const UserView = () => {
  // ** Store Vars
  const store = useSelector((state) => state.users)
  const dispatch = useDispatch()

  // ** Hooks
  const { id } = useParams()

  // ** Hooks
  const { t } = useTranslation()

  // ** Get on mount
  useEffect(() => {
    // console.log(parseInt(id))
    // if (!store.isLoadingDetail) {
    // console.log(store.selectedUser?.id !== parseInt(id))
    if (
      !store.selectedUser ||
      (store.selectedUser?.id !== parseInt(id) && !store.isLoadingDetail)
    ) {
      dispatch(getUser(parseInt(id)))
    }
    // }
  }, [])

  const [active, setActive] = useState("1")

  const toggleTab = (tab) => {
    if (active !== tab) {
      setActive(tab)
    }
  }

  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
        <CardText className="mb-0 mt-1">{t("Please Wait")}...</CardText>
      </Fragment>
    )
  }
  return !store.isLoadingDetail ? (
    store.selectedUser !== null && store.selectedUser !== undefined ? (
      <div className="app-user-view">
        <Row>
          <Col xl="4" lg="5" xs={{ order: 1 }} md={{ order: 0, size: 5 }}>
            <UserInfoCard selectedUser={store.selectedUser} />
            {/* <PlanCard /> */}
          </Col>
          <Col xl="8" lg="7" xs={{ order: 0 }} md={{ order: 1, size: 7 }}>
            <UserTabs active={active} toggleTab={toggleTab} />
          </Col>
        </Row>
      </div>
    ) : (
      <Alert color="danger">
        <h4 className="alert-heading">{t("User not found")}</h4>
        <div className="alert-body">
          {t("User with id")}: {id}{" "}
          {t("doesn't exist. Check list of all Users")}:{" "}
          <Link to="/apps/user/list">{t("Users List")}</Link>
        </div>
      </Alert>
    )
  ) : (
    <Fragment>
      <div className="app-user-view">
        <UILoader
          className="mt-5"
          blocking={store.isLoadingDetail}
          loader={<Loader />}
        ></UILoader>
      </div>
    </Fragment>
  )
}
export default UserView
